<template>
    <div class="hui-cancel-apply">
        <apply ref="child" :dataCopy="infoData" :mapCopy="map"></apply>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="confirm">撤销申请</mt-button>
        </div>
    </div>
</template>

<script>
    import Apply from '../apply';
    export default {
        name: 'hui-cancel-apply',
        components: {
            Apply
        },
        data() {
            return {
                infoData: {},
                Map: {
                    'one': [
                        { key: 'account', title: '信用卡卡号' },
                        { key: 'AMOUNT', title: '交易金额', type: 'amount-unit', style: { color: '#FF942F' } },
                        { key: 'NBRMTHS', title: '分期期数' }
                    ],
                    'two': [
                        { key: 'account', title: '信用卡卡号' },
                        { key: 'OUTCARDNO', title: '借记卡卡号' },
                        { key: 'AMOUNT', title: '分期金额', type: 'amount-unit', style: { color: '#FF942F' } },
                        { key: 'num', title: '分期期数' }
                    ]
                }
            };
        },
        computed: {
            ls() {
                return this.$ls.getItem('credit-hui-record');
            },
            map() {
                if (this.ls.module === 'A' || this.ls.module === 'Y') {
                    return this.Map['one'];
                }
            }
        },
        created() {
            this.infoData = Object.assign({}, this.ls);
            this.infoData.NBRMTHS = this.infoData.NBRMTHS + '期';
        },
        methods: {
            confirm() {
                // this.$router.push({ name: 'CreditHuiResult' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        clear: both;
        margin-top: 116px;
    }
</style>


