<template>
	<div class="more-service-apply-process-detail">
		<div class="products">
			<div v-for="item in list" class="product-item">
				<mt-cell title="信用卡卡号：" :value="item.CARDNO"></mt-cell>
				<mt-cell title="申请进度：" :value="item.APPSTATUS | commFilter('CREDIT_APPLY_STATUS')"></mt-cell>
			</div>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回信用卡首页</mt-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			list: {}
		};
	},
	methods: {
		back() {
			this.$router.go(-2);
		}
	},
	created() {
		this.list = this.$route.params.list;
	}
};

</script>

<style lang="scss">
	
.more-service-apply-process-detail{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
}

</style>


