<template>
	<div class="card-set-success">
		<tip-page :tipTitle="msg" :tipButton="tipButton" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipButton: ''
		};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'rename'){
				this.tipButton = '返回卡片设置';
				return '别名修改成功';
			}else if(type === 'cost'){
				this.tipButton = '返回信用卡管理';
				return '消费凭密修改成功';
			}else{
				this.tipButton = '返回信用卡管理';
				return '卡片删除成功';
			}
		}
	},
	methods: {
		back() {
			if(this.$route.params.type === 'rename'){
				this.$router.go(-2);
			} else{
				this.$router.go(-2);
			}
		}
	}
};

</script>

<style lang="scss">
.card-set-success{
}
</style>


