<template>
    <div class="hui-record-list">
        <template>
            <div class="date-select" v-if="!flag">
                <p>2017年05月10日</p>-
                <p>2017年05月30日</p>
                <i class="icon-date-select"></i>
            </div>

            <div class="field-top">
                {{topTitle}}
            </div>

            <template v-for="item in listData">
                <cell title="" @click.native="detail(item)">
                    <i slot="icon" :class="'icon icon-menu-' + icon"></i>
                    <div class="title" slot="title">{{item.TRANTYPE}}（{{item.NBRMTHS}}期）</div>
                    <div class="label" slot="label">{{item.PURDATE1}}</div>
                    <div class="value" slot="value">{{item.AMOUNT | formatNumber(2)}}元</div>
                </cell>
            </template>
        </template>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import { mapGetters } from 'vuex';
    export default {
        name: 'hui-record-list',
        data() {
            return {
                listData: []
            };
        },
        computed: {
            ...mapGetters(['creditMap', 'innerMap']),
            flag() {
                let type = this.$route.query.t;
                if (type === 'cancel') {
                    return true;
                } else {
                    return false;
                }
            },
            topTitle() {
                return this.flag ? '请选择要撤销的分期' : '请选择要查询的分期';
            },
            href() {
                return this.flag ? 'CreditHuiRecordCancel' : 'CreditHuiRecordApportioned';
            },
            icon() {
                let ls = this.$ls.getItem('credit-hui-record');
                return this.creditMap[ls.module].no;
            }
        },
        created() {
            if (this.$route.query.t === 'cancel') {
                this.queryCancelList();
            } else {
                this.queryApportionedList();
            }
        },
        methods: {
            detail(item) {
                let ls = this.$ls.getItem('credit-hui-record');
                let history = {
                    account: ls.account,
                    module: ls.module
                };
                let obj = Object.assign({}, history, item);
                this.$ls.setItem('credit-hui-record', obj);
                this.$router.push({ name: this.href });
            },
            queryCancelList() {
                let ls = this.$ls.getItem('credit-hui-record');
                let params = {
                    DJKKH: ls.account, // 信用卡卡号
                    CREDNO: this.creditMap[ls.module].credit_no,  // 信用计划编号
                    showLoading: '加载中...',
                    MPSTS: '1'  // 撤销
                };
                services.queryHuiList(params)
                    .then(res => {
                        this.listData = res.data;
                    }, err => {
                        console.log(err);
                    });
            },
            queryApportionedList() {
                let ls = this.$ls.getItem('credit-hui-record');
                let params = {
                    DJKKH: ls.account, // 信用卡卡号
                    STARTDATE: '',
                    ENDDATE: '',
                    showLoading: '加载中...',
                    MPTYPE: ''
                };
                services.queryApportionedList(params)
                    .then(res => {
                        this.listData = res.data;
                    }, err => {
                        console.log(err);
                    });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-hui-record');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .date-select {
        padding: 28px 50px;
        background-color: #fff;

        p {
            display: inline-block;
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #272727;
            height: 44px;
            line-height: 44px;
        }

        .icon-date-select {
            display: inline-block;
            margin-top: 4px;
            float: right;
            height: 32px;
            width: 32px;
            @include image-set('../../../../../assets/img/credit/Shape');
        }
    }

    .field-top {
        padding: 20px 50px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #666666;
        line-height: 36px;
    }

    .icon {
        position: initial;
        top: initial;
        left: initial;
        transform: initial;
        display: inline-block;
        width: 60px;
        height: 60px;
        margin-right: 30px;
    }

    .title {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #272727;
        letter-spacing: 0;
        line-height: 44px;
        /* width: 30.1%; */
    }

    .label {
        margin-top: 10px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #B5B5B5;
        line-height: 36px
    }

    .value {
        font-family: SFUIText-Regular;
        font-size: 17px;
        color: #272727;
        line-height: 48px;
    }
</style>


