<template>
    <div class="bill">
        <div class="field-part">
            <mt-cell class="allow-down" title="信用卡卡号" @click.native="showCreditAccount()" v-model="formData.PAYER_ACCOUNT" is-link></mt-cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <div class="panel">
                <div class="panel-top">
                    <p class="title">剩余未还（元）</p>
                    <p class="amount">{{infoData.RESIDUE_REFUND_AMOUNT | formatNumber(2)}}</p>
                    <p class="late-day">最终还款日：{{infoData.REFUND_DATE}}</p>
                </div>
                <div class="panel-bottom">
                    <p class="now">本期应还：{{infoData.CURRENT_REFUND_AMOUNT | formatNumber(2)}}元</p>
                    <p class="low">最低还款：{{infoData.MINIMUM_AMT | formatNumber(2)}}元</p>
                </div>
            </div>
        </div>

        <div class="field-part">
            <account-select title='借记卡卡号' :value='formData.PAYEE_ACCOUNT' :balance="formData.PAYEE_ACCOUNT_BALANCE" @click.native="showDebitAccount()"></account-select>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
            <cell-transfer-money title="还款金额" placeholder="请输入还款金额" @focus="amountFocus()" @blur="amountBlur()" validate="还款金额大于借记卡金额"
                :totalMoney="debitAccount.checked.BALANCE" v-model="formData.TRANAMT">
            </cell-transfer-money>

            <div v-if="showAmount">
                <div class="cover"></div>
                <ul class="amount-wrap">
                    <li class="item" @click.stop.prevent="setAmount('RESIDUE_REFUND_AMOUNT')">
                        <span class="title">全额还款</span>
                        <span class="value">￥{{infoData.RESIDUE_REFUND_AMOUNT | formatNumber(2)}}</span>
                    </li>
                    <li class="item" @click="setAmount('MINIMUM_AMT')">
                        <span class="title">最低还款</span>
                        <span class="value">￥{{infoData.MINIMUM_AMT | formatNumber(2)}}</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="next">下一步</mt-button>
        </div>
        <div class="warn">
            手头有点紧?可使用惠分单分期哦
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="test">为他人还款</mt-button>
        </div>
    </div>
</template>

<script>
    import * as servicesC from '@/services/credit/credit';
    import * as services from '@/services/public';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    PAYER_ACCOUNT: '',
                    ACCT_NAME: '',
                    ACCT_TYPE: '',
                    PAYEE_ACCOUNT: '',
                    COR_NAME: '',
                    INCARDTYPE: '',
                    PAYEE_ACCOUNT_BALANCE: '',
                    TRANAMT: ''
                },
                infoData: {},
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                showAmount: false,
                clickable: false
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-repayment');
            if (ls != null) {
                this.formData.TRANAMT = ls.TRANAMT;
            }
            servicesC.queryCreditList()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                    let _data;
                    if (ls == null) {
                        _data = res.data.filter(item => {
                            return item.IS_DEFAULT === '1';
                        })[0] || {};
                    } else {
                        _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYER_ACCOUNT;
                        })[0] || {};
                    }
                    this.creditAccount.checked = _data;
                    this.infoData = _data;
                    this.formData.PAYER_ACCOUNT = _data.ACCOUNT;
                    this.formData.ACCT_NAME = _data.ACCT_NAME;
                    this.formData.ACCT_TYPE = _data.ACCT_TYPE;
                }, (err) => {
                    console.log(err);
                });
            services.getCommonAccountList()
                .then((res) => {
                    this.debitAccount.accountList = res.LIST;
                    let _data;
                    if (ls == null) {
                        _data = res.LIST.filter(item => {
                            return item.IS_DEFAULT === '1';
                        })[0] || {};
                    } else {
                        _data = res.LIST.filter(item => {
                            return item.ACCOUNT === ls.PAYEE_ACCOUNT;
                        })[0] || {};
                    }
                    this.debitAccount.checked = _data;
                    this.formData.PAYEE_ACCOUNT = _data.ACCOUNT;
                    this.formData.COR_NAME = _data.ACCT_NAME;
                    this.formData.INCARDTYPE = _data.ACCT_TYPE;
                    this.formData.PAYEE_ACCOUNT_BALANCE = _data.BALANCE;
                }, (err) => {
                    console.log(err);
                });
        },
        watch: {
            'formData.TRANAMT': {
                handler: 'validate',
                deep: true
            }
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.infoData = data.item;
                    this.formData.PAYER_ACCOUNT = this.creditAccount.checked.ACCOUNT;
                    this.formData.ACCT_NAME = this.creditAccount.checked.ACCT_NAME;
                    this.formData.ACCT_TYPE = this.creditAccount.checked.ACCT_TYPE;
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.formData.PAYEE_ACCOUNT = this.debitAccount.checked.ACCOUNT;
                    this.formData.COR_NAME = this.debitAccount.checked.ACCT_NAME;
                    this.formData.INCARDTYPE = this.debitAccount.checked.ACCT_TYPE;
                    this.formData.PAYEE_ACCOUNT_BALANCE = this.debitAccount.checked.BALANCE;
                }, (data) => {
                });
            },
            amountFocus() {
                // this.showAmount = true;
            },
            setAmount(key) {
                this.formData.AMOUNT = this.infoData[key];
                this.showAmount = false;
            },
            amountBlur() {
                // this.showAmount = false;
            },
            validate(val) {
                if (this.$validate.isEmpty(val)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            },
            next() {
                this.$router.push({ name: 'CreditRepaymentPay' });
            },
            test() {
                this.$router.push({ name: 'CreditRepaymentOther' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-repayment', this.formData);
            } else {
                this.$ls.removeItem('credit-repayment', this.formData);
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .panel {
        padding: 30px 0 0;
        background-color: #fff;

        .panel-top {
            position: relative;
            padding-bottom: 30px;
            margin: 0 50px;
            text-align: center;
            @include mini-border(bottom, #E4E5E6, after);

            .title {
                font-family: PingFangSC-Regular;
                font-size: 13px;
                color: #999999;
                line-height: 36px;
            }
            .amount {
                margin: 10px 0;
                font-family: SFUIDisplay-Regular;
                font-size: 30px;
                color: #272727;
                line-height: 72px;
            }

            .late-day {
                font-family: PingFangSC-Regular;
                font-size: 13px;
                color: #999999;
                line-height: 36px;
            }
        }

        .panel-bottom {
            padding: 30px 60px;
            width: 100%;
            display: table;

            p {
                font-family: PingFangSC-Regular;
                display: table-cell;
                *display: inline-block;
                opacity: 0.6;
                font-size: 13px;
                color: #272727;
                height: 36px;
                line-height: 36px;
            }

            .low {
                text-align: right;
            }
        }
    }

    .amount-wrap {
        background-color: #fff;

        .item+.item {
            @include mini-border(top, #E4E5E6, before);
        }

        .item {
            position: relative;
            height: 100px;
            margin-left: 100px;

            .title {
                font-family: PingFangSC-Regular;
                font-size: 15px;
                color: #272727;
                line-height: 100px;
            }

            .value {
                margin-left: 30px;
                font-family: PingFangSC-Regular;
                font-size: 15px;
                color: #308CF7;
                line-height: 100px;
            }
        }
    }

    .field-action {
        margin-top: 80px;
    }

    .warn {
        text-align: center;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #308CF8;
        line-height: 36px;
    }
</style>


