<template>
    <div class="hui-record">
        <mt-cell title="可撤销分期" @click.native="doList('cancel')" is-link></mt-cell>
        <mt-cell title="已分摊分期" @click.native="doList('apportioned')" is-link></mt-cell>
    </div>
</template>

<script>
    export default {
        name: 'hui-record',
        methods: {
            doList(query) {
                this.$router.push({ name: 'CreditHuiRecordList', query: { t: query } });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-hui-record');
            }
            next(true);
        }
    };

</script>


