<template>
    <div class="hui-apportioned">
        <cell-map :dataCopy="infoData" :map="map"></cell-map>
    </div>
</template>

<script>
    import Apply from '../apply';
    export default {
        name: 'hui-apportioned',
        components: {
            Apply
        },
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'account', title: '信用卡卡号' },
                    { key: 'AMOUNT', title: '交易金额', type: 'amount-unit', style: { color: '#FF942F' } },
                    { key: 'NBRMTHS', title: '分期期数' }
                ],
                mapW: [
                    { key: 'account', title: '信用卡卡号' },
                    { key: 'OUTCARDNO', title: '借记卡卡号' },
                    { key: 'AMOUNT', title: '分期金额', type: 'amount-unit', style: { color: '#FF942F' } },
                    { key: 'num', title: '分期期数' }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-hui-record');
            this.infoData = Object.assign({}, ls);
            console.log(ls);
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        clear: both;
        margin-top: 116px;
    }
</style>


