<template>
    <div class="public-list">
        <template v-if="dataCopy.length > 0">
            <div class="field-top">
                选择要分期的交易
            </div>

            <template v-for="item in listData">
                <cell title="" @click.native="doHref(item)">
                    <div class="title" slot="title">{{item.TRANTYPE}}</div>
                    <div class="label" slot="label">{{item.PURDATE1}}</div>
                    <div class="value" slot="value">{{item.AMOUNT | formatNumber(2)}}元</div>
                </cell>
            </template>
        </template>

        <template v-else>
            <div class="no-data">
                <i class="icon"></i>
                <p class="msg">暂无可分期的交易</p>
                <p class="desc">单笔满500元(含)以上的消费才可以进行分期</p>
            </div>
        </template>

    </div>
</template>

<script>
    export default {
        name: 'public-list',
        props: ['dataCopy'],
        computed: {
            listData() {
                // console.log(this.dataCopy);
                return this.dataCopy;
            }
        },
        methods: {
            doHref(item) {
                this.$emit('emit-href', item);
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        padding: 20px 50px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #666666;
        line-height: 36px;
    }

    .title {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #272727;
        letter-spacing: 0;
        line-height: 44px;
    }

    .label {
        margin-top: 10px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #B5B5B5;
        line-height: 36px
    }

    .value {
        font-family: SFUIText-Regular;
        font-size: 17px;
        color: #272727;
        line-height: 48px;
    }

    .no-data {
        margin: 260px 0 0;
        text-align: center;

        .icon {
            height: 244px;
            width: 180px;
            display: inline-block;
            @include image-set('../../../../assets/img/bank/transaction/no-record');
        }

        .msg {
            margin-top: 40px;
            font-family: PingFangSC-Medium;
            font-size: 17px;
            color: #666666;
            height: 48px;
            line-height: 48px;
        }

        .desc {
            margin-top: 10px;
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #B5B5B5;
            height: 36px;
            line-height: 36px;
        }
    }
</style>


