<template>
    <div class="consume">
        <index ref="child" ls="credit-borrow-money">
            <div class="amount" slot="amount">
                <p class="p">惠借钱可用额度(元)</p>
                <p class="num">{{this.amountData.CALIMITAVB | formatNumber(2)}}</p>
                <p class="p-num">惠借钱额度 {{this.amountData.CALIMIT | formatNumber(2)}}</p>
            </div>

            <template slot="info">
                <p class="info">您获得核准的惠提现额度后，可申请转入个人借记卡，并在约定期限内偿还。</p>
            </template>
            <div class="field-action" slot="btn">
                <mt-button type="primary" size="large" @click="doApply">我要申请</mt-button>
            </div>
            <template slot="process">
                <p class="title">
                    办理流程：
                </p>
                <p>
                    农商银行核准额度>>>持卡人申请>>>转入借记卡>>>提现>>>分期偿还
                </p>
            </template>
            <template slot="warn">
                <p class="title">
                    温馨提示：
                </p>
                <p>
                    1. 公务卡不支持该交易；
                </p>
                <p>
                    2. 惠借钱起点金额为5000元，以1000的倍数递增；
                </p>
                <p>
                    3. 您可选择6、12、24、36期进行惠借钱分期，一个月为一期；
                </p>
                <p>
                    4. 惠借钱撤销仅限当日处理。
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        data() {
            return {
                amountData: {
                    CALIMITAVB: '',
                    CALIMIT: ''
                }
            };
        },
        mounted() {
            let params = {
                DJKKH: this.$refs.child.formData.account
            };
            services.queryWithdrawalsLimit(params)
                .then(res => {
                    this.amountData = {
                        CALIMIT: res.data.CALIMIT,
                        CALIMITAVB: res.data.CALIMITAVB
                    };
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            doApply() {
                this.$router.push({ name: 'CreditBorrowMoneyCommit' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                let obj = Object.assign({}, this.$refs.child.formData, this.amountData);
                this.$ls.setItem('credit-borrow-money', obj);
            } else {
                this.$ls.removeItem('credit-borrow-money');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .amount {
        height: 244px;
        @include image-set('../../../../assets/img/credit/hui-series/gold-bg');
        background-size: 100% 100%;
    }
</style>


