/*  我要还款  */
export function doRepayment(data) {
    return ajax.postAfterLoginUpdate('026004', data, api.doRepayment);
}

/*  惠消费:可灵活分期交易列表查询  1-撤销， 2-申请 */
export function queryHuiList(data) {
    return ajax.postAfterLoginQueryList('015012', data, api.queryHuiList[data.MPSTS]);
}

/*  获取惠消费分期信息 0-申请,1-撤销 2-预申请 3-手续费 */
export function queryConsumeApply(data) {
    // return ajax.postAfterLoginUpdate('015013', data, api.queryConsumeApply);
    return ajax.postAfterLoginUpdate('015013', data, api.queryConsumeApply[data.OPTION]);
}

/*  获取惠分单分期信息 0-申请 3-额度查询 4-手续费 */
export function queryOrderStagesApply(data) {
    return ajax.postAfterLoginQuery('015037', data, api.queryOrderStagesApply[data.OPTION]);
}

/*  获取惠提现-惠借钱  可用额度信息 */
export function queryWithdrawalsLimit(data) {
    return ajax.postAfterLoginQuery('015001', data, api.queryWithdrawalsLimit);
}

/*  获取惠提现申请信息 3-手续费 0-申请 */
export function queryWithdrawalsApply(data) {
    return ajax.postAfterLoginQuery('015014', data, api.queryWithdrawalsApply[data.OPTION]);
}

/*  获取惠借钱分期信息  3-手续费 0-申请  */
export function queryBorrowMoneyApply(data) {
    return ajax.postAfterLoginQuery('015016', data, api.queryBorrowMoneyApply[data.OPTION]);
}

/*  获取惠系列可分摊列表信息  B：现金分期  L：大宗分期  */
export function queryApportionedList(data) {
    return ajax.postAfterLoginQueryList('015011', data, api.queryApportionedList);
}


/*  预借现金  */
export function applyCashAdvance(data) {
    return ajax.postAfterLoginUpdate('026005', data, api.applyCashAdvance);
}


